der-text[data-v-fa58973a] {\n    color: var(--Plateau-Navy, #151233);\n    font-family: Roboto;\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: capitalize;\n}\n.table-div[data-v-fa58973a] {\n    margin-top: 12px;\n    padding: 0 20px;\n}\n.members-table[data-v-fa58973a] {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\n.members-table td[data-v-fa58973a]:hover {\n    background-color: none !important;\n}\n.members-table tbody>tr[data-v-fa58973a]:hover {\n    background-color: none !important;\n    border: none !important;\n}\n.members-table th[data-v-fa58973a] {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 17px 86px 17px 36px;\n}\n.members-table td[data-v-fa58973a] {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 13px 86px 13px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n.members-table .thead[data-v-fa58973a] {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n.member-edit-menu-button[data-v-fa58973a] {\n    font-size: 24px;\n    line-height: 24px;\n    text-align: center;\n    color: #757575;\n    cursor: pointer;\n}\n.member-edit-menu-button[data-v-fa58973a]::after {\n    border: unset !important;\n}\n.member-edit-menu[data-v-fa58973a] {\n    background: #FFFFFF;\n    padding: 12px 0;\n    color: #212121;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    font-size: 14px;\n    line-height: 16px;\n    width: max-content;\n}\n.member-edit-menu li[data-v-fa58973a] {\n    padding: 10px;\n}\n.member-edit-menu span[data-v-fa58973a] {\n    display: flex;\n    align-items: center;\n    cursor: pointer;\n    height: 30px;\n    padding: 0 12px;\n    position: relative;\n}\n.member-edit-menu li[data-v-fa58973a]:hover {\n    background-color: #d5d5d5;\n    cursor: pointer;\n}\n.member-edit-menu li.disabled[data-v-fa58973a] {\n    /* pointer-events: none; */\n    opacity: 0.5;\n    cursor: default;\n}\n.member-edit-menu:has(.remove-from-organization:hover) .remove-from-organization-tooltip[data-v-fa58973a],\n.member-edit-menu:has(.remove-owner:hover) .remove-owner-tooltip[data-v-fa58973a] {\n    visibility: visible;\n}\n.member-edit-menu .remove-from-organization-tooltip[data-v-fa58973a],\n.member-edit-menu .remove-owner-tooltip[data-v-fa58973a] {\n    position: absolute;\n    top: -60px;\n    padding: 10px;\n    background-color: #757575;\n    color: #FFF;\n    height: auto;\n    font-size: 12px;\n    border-radius: 4px;\n    box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.20);\n    visibility: hidden;\n}\n.member-edit-menu .remove-owner-tooltip[data-v-fa58973a] {\n    top: 12px;\n}\n.member-edit-menu span.disabled[data-v-fa58973a]:hover::after {\n    visibility: visible;\n}\n.member-edit-menu .disabled:hover .menu_tooltip[data-v-fa58973a] {\n    visibility: visible;\n}\n.main-group[data-v-fa58973a] {\n    display: flex;\n    padding: 32px 0;\n}\n.content[data-v-fa58973a] {\n    background-color: white;\n    height: calc(100vh - 128px);\n    display: flex;\n    width: 100%;\n}\n.leftnavbuttontext.selected[data-v-fa58973a] {\n    color: #151233;\n}\n.leftnavbuttontext[data-v-fa58973a] {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 20px;\n    line-height: 23px;\n    color: #A1A1A1;\n    flex: none;\n    order: 1;\n    flex-grow: 0;\n}\n.leftnavbuttonicon[data-v-fa58973a] {\n    font-size: 24px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    padding: 0px;\n    gap: 10px;\n    width: 24px;\n    height: 24px;\n    flex: none;\n    order: 0;\n    flex-grow: 0;\n}\n.leftnav[data-v-fa58973a] {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n    padding: 32px 0px;\n    height: 100%;\n    border-radius: 11px 0px 0px 11px;\n}\n.leftnavbutton.selected[data-v-fa58973a] {\n    background: #FFFFFF;\n    /* Plateau Navy */\n    border-left: 5px solid #151233;\n}\n.leftnavbutton[data-v-fa58973a] {\n    box-sizing: border-box;\n    width: -webkit-fill-available;\n    height: 56px;\n    display: flex;\n    flex-direction: row;\n    align-items: flex-start;\n    padding: 16px;\n    gap: 8px;\n    cursor: pointer;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/main/manage.vue"],"names":[],"mappings":";AAiQA;IACI,qBAAqB;AACzB;AAEA;IACI,eAAe;IACf,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;AACvB;AAGA;IACI,eAAe;IACf,cAAc;IACd,gBAAgB;IAChB,iBAAiB;IACjB,eAAe;AACnB;AAGA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,0BAA0B;IAC1B,gCAAgC;IAChC,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;IACnB,iBAAiB;IACjB,aAAa;IACb,mBAAmB;AACvB;AAEA;IACI,eAAe;IACf,cAAc;AAClB;AAEA;IACI,mBAAmB;IACnB,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;IACd,iBAAiB;IACjB,YAAY;AAChB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;AAClB;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,YAAY;AAChB;AAEA;IACI,eAAe;IACf,oBAAoB;AACxB;AAEA;IACI,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,WAAW;AACf;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;AAClB;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,cAAc;IACd,kBAAkB;IAClB,sCAAsC;IACtC,yBAAyB;AAC7B;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,eAAe;AACnB;AAEA;IACI,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,8CAA8C;IAC9C,6BAA6B;IAC7B,oBAAoB;IACpB,mCAAmC;IACnC,kBAAkB;IAClB,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;IACzB;AACJ;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,uDAAuD;AAC3D;AAEA;IACI,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IA