../../common/qCloudTemp/authentication\";\nimport { IListInvitationsResponseItem } from \"../../../../../../common/qCloudTemp/symDtoObjects\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n\nconst activeUsersTab = ref(true);\nconst invitedUsersTab = ref(false);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst members = ref<Array<IUser_SUSI>>([]);\nconst waitingMembers = ref<Array<IListInvitationsResponseItem>>([]);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst activeSearch = ref(\"\");\nconst invitedSearch = ref(\"\");\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\n\nconst storeMembers = async () => {\n    executor.execute(async () => {\n        await refreshOrganizationInvitationsData();\n        await refreshOrganizationUsersData();\n    }, { loading: true });\n}\n\nconst refreshOrganizationInvitationsData = async () => {\n    const response = await organization.listOrganizationInvitations();\n    waitingMembers.value = [];\n    response.forEach(member => waitingMembers.value.push(member));\n};\n\n\nconst refreshOrganizationUsersData = async () => {\n    const response = await organization.listOrganizationUsers();\n    members.value = [];\n    response.forEach(member => members.value.push(member));\n};\n\nconst cancelInvitation = async (invitationId: string) => {\n    executor.execute(async () => {\n        await organization.deleteInvitation({ invitationId });\n        await storeMembers();\n    }, { loading: true });\n};\nconst filteredMembers = computed(() => {\n    return members.value.filter((user: IUser_SUSI) => (user.name?.toUpperCase().includes(activeSearch.value.toUpperCase()) ||\n        user.lastName?.toUpperCase().includes(activeSearch.value.toUpperCase()) ||\n        user.email?.toUpperCase().includes(activeSearch.value.toUpperCase()) ||\n        user.role?.toUpperCase().includes(activeSearch.value.toUpperCase())) || members.value\n    );\n})\n\nconst isOwner = (userId?: string) => {\n    if (!userId) {\n        return;\n    }\n    return viewModel.user!.selectedOrganization?.ownerUserIds ? viewModel.user!.selectedOrganization.ownerUserIds.includes(userId) : viewModel.user!.selectedOrganization?.creatorUserId === userId\n};\n\nfunction calculateDate(createDate: string, format: boolean) {\n    if (!format) {\n        const date = new Date(createDate);\n        if (date) {\n            const minutes = parseInt(String((date.getTime() - (new Date()).getTime()) / (60 * 1000)), 10)\n            const hours = parseInt(String(minutes / 60), 10);\n            const days = parseInt(String(hours / 24), 10);\n            const hoursCalc = hours - (days * 24);\n            if (hours > 0) {\n                return days.toFixed() + ` day${days < 2 ? '' : 's'} ` + hoursCalc.toFixed() + ` hour${hoursCalc < 2 ? '' : 's'} `;\n            }\n\n            else {\n                return \"< 1 hour\";\n            }\n        }\n    }\n    else {\n        const formatter = new Intl.DateTimeFormat('en-GB', { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric' });\n        const formattedDate = formatter.format(new Date(createDate));\n        return formattedDate;\n    }\n\n}\n\nconst removeFromOrganization = async (userId: string) => {\n    if (isOwner(userId)) { return; }\n    executor.execute(async () => {\n        await organization.removeUserFromOrganization({ userId });\n        await storeMembers();\n    }, { loading: true });\n};\nconst updateOrganizationOwner = async (userId: string, action: \"add\" | \"remove\") => {\n    executor.execute(async () => {\n        await organization.updateOrganizationOwner({ userId, action });\n    }, { loading: true });\n}\nfunction openInvitePeopleDialog() {\n    if (viewModel.user?.selectedOrganization?.accountType == \"premium\") {\n        const closeCb = async (reason: \"close\" | \"outclick\" | \"component\") => {\n            if (reason == \"component\") {\n                await storeMembers();\n            }\n        }\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n            { closable: true, title: \"Invite People to \" + selectedOrganizationName.value, width: \"658px\", maxHeight: \"700px\", closeCb },\n\n        );\n    }\n    else {\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`../../components/dialogs/premium/premiumAccountForm.vue`)),\n            { closable: true, image: {src: \"https://stechq.github.io/cdn/Idetemplates/plateauLogo-premium.svg\", width: \"134px\", height: \"40px\"},\n            width: \"1022px\", maxHeight: \"688px\", customStyle: {headerStyle: \"background: #1B4874; border-bottom: 1px solid #E3E3E3; padding: 20px; height: 80px; display: flex; align-items: center;\", closeIcon: \"color: #FFFFFF\"} }\n        );\n    }\n}\n\nexecutor.execute(async () => { await storeMembers() }, { loading: true });\n\n</script>\n<template>\n    <div class=\"main-group\">\n        <div class=\"leftnav\">\n            <div class=\"leftnavbutton selected\">\n                <i class=\"mdi mdi-account-multiple-plus-outline leftnavbuttonicon\"></i>\n                <span class=\"leftnavbuttontext selected\">Members</span>\n            </div>\n        </div>\n\n        <div class=\"content\">\n            <div style=\"width: 100%;\">\n                <div class=\"header\">\n                    <span class=\"header-text\">Members</span>\n                    <span class=\"description-text\">Users description will be here, it can be much longer than this text\n                        sample.</span>\n                </div>\n                <div class=\"members-tab\">\n                    <div @click=\"activeUsersTab = true; invitedUsersTab = false\"\n                        :class=\"activeUsersTab ? 'active-category' : ''\">\n                        Active</div>\n                    <div @click=\"invitedUsersTab = true; activeUsersTab = false\"\n                        :class=\"invitedUsersTab ? 'active-category' : ''\">\n                        Invited</div>\n                </div>\n                <template v-if=\"activeUsersTab\">\n                    <div class=\"members-actions\">\n                        <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"activeSearch\" />\n                        <button class=\"invite-button\" @click=\"openInvitePeopleDialog\">INVITE</button>\n                    </div>\n                    <div class=\"table-div\">\n                        <table class=\"members-table\">\n                            <thead>\n                                <tr class=\"thead\">\n                                    <th class=\"col\">Email Adress</th>\n                                    <th class=\"col\">Name</th>\n                                    <th class=\"col-xl-2\">Role</th>\n                                    <!-- <th class=\"col\">Date Joined</th> -->\n                                    <th class=\"col-xl-1\"></th>\n                                </tr>\n                            </thead>\n                            <tbody>\n                                <tr v-for=\"member in filteredMembers\" :key=\"member.id\">\n                                    <td><img src=\"../../../../images/profile-image-dark-in-menu.svg\"\n                                            style=\"margin-right: 12px;\" />{{\n                                                member.email\n                    